	module.exports = {"data":{"markdownRemark":{"html":"<p> <strong>WHEREAS</strong> </p>\n<ul>\n<li>\n<p>the Truth and Reconciliation Commission Call to Action 19 states: We call upon the federal government, in consultation with Indigenous peoples, to establish measurable goals to identify and close the gaps in health outcomes between Indigenous and non-Indigenous communities, and to publish annual progress reports and assess long-term trends;</p>\n</li>\n<li>\n<p>Section 24.2 of the United Nations Declaration on the Rights of Indigenous Peoples state: Indigenous individuals have an equal right to the enjoyment of the highest attainable standard of physical and mental health. States shall take the necessary steps with a view to achieving progressively the full realization of this right. </p>\n</li>\n</ul>\n<p><strong>BE IT RESOLVED</strong> the Liberal party of Canada urge the Government of Canada to create: </p>\n<ul>\n<li>\n<p>an independent Indigenous Health Care Auditor that reviews and reports toIndigenous peoples and to Parliament on the progress and failures, including health care treatment and health care quality indicators, of the federal government on providing health care services and treatment toIndigenouspeoples;</p>\n</li>\n<li>\n<p>an independent national Indigenous Health Care Ombudsman. The ombudsman could respond to unresolved complaints from Indigenous patients, residents and clients about their health care experience at a health sector organization, which includes hospitals, long-term care homes or community and local health centres.(Indigenous Peoples’ Commission)</p>\n</li>\n</ul>","frontmatter":{"path":"/policy/indigenous-health-care-auditor-and-indigenous-health-ombudsman","date":"Tue Mar 06 2018 00:08:44 GMT-0500 (EST)","title":"Indigenous Health Care Auditor and Indigenous Health Ombudsman"}}},"pathContext":{}}

